import React from "react";
import { View, Text, PanResponder, StyleSheet } from "react-native";
import { Entypo } from '@expo/vector-icons';

const ProcessNodeDisplayStyle = StyleSheet.create({
    container: {
		position: "absolute",
        alignSelf: "flex-start",
        width: 150,
        padding: 5,
        backgroundColor: "white"
    },
    connector: {
        width: 20,
        height: 20,
        borderRadius: 20/2
    },
	inContent: {
		flexDirection: "row",
        alignSelf: "flex-start"
    },
    inConnector: {
        backgroundColor: "green"
    },
	outContent: {
		flexDirection: "row",
        alignSelf: "flex-end",
    },
    outConnector: {
        backgroundColor: "blue"
    }
})

const ProcessNodeDisplayEnterConnector = (props) => {
	const connectorDir = props.direction == "in" ? "green" : "blue";

    return (
        <Entypo name="arrow-right" size={18} color={ connectorDir } />
    )
}

export const ProcessNodeDisplay = (props) => {
    const panResponder = React.useMemo(() => PanResponder.create({
        onStartShouldSetPanResponder: (evt, gestureState) => true,
        onPanResponderMove: (evt, gestureState) => {
            console.log("GOGOGO")
        }        
    }), []);

    return (
        <View style={ [ ProcessNodeDisplayStyle.container, { top: props.position.x, left: props.position.y }] } {...panResponder.panHandlers}>
            <View>
                <Text>
                    { props.desc }
                </Text>
            </View>
            <View style={{ flexDirection: "row" }}>
				<View style={{ flex: 1 }}>
					<View style={ ProcessNodeDisplayStyle.inContent }>
						<ProcessNodeDisplayEnterConnector direction="in" />
					</View>
					<View style={ ProcessNodeDisplayStyle.inContent }>
						<ProcessNodeDisplayEnterConnector direction="in" />
					</View>
				</View>
				<View style={{ flex: 1 }}>
					<View style={ ProcessNodeDisplayStyle.outContent }>
						<ProcessNodeDisplayEnterConnector direction="out" />
					</View>
				</View>
            </View>
        </View>
    )
}